<template>
    <nav class="navbar navbar-expand-md navbar-dark bg-dark">

        <ul class="navbar-nav mr-auto">
                <template v-if="auth">
                    <li class="nav-item">
                        <router-link to="/" class="nav-link">Home</router-link>
                    </li>
                </template>
            <li class="nav-item">
                <template v-if="auth">
                    <li class="nav-item">
                    <router-link to="/profile" class="nav-link">Profile</router-link>
                    </li>
                </template>
            </li>
        </ul>
               
        <ul class="navbar-nav my-2 my-lg-0">
          <template v-if="auth">
             <li class="nav-item">
                    <router-link to="#" class="nav-link" @click="logout">Logout</router-link>
                </li>
           </template>   
            <template v-if="!auth">
                <li class="nav-item">
                    <router-link to="/login" class="nav-link">Login</router-link>
                </li>
            </template>
        </ul>
    </nav>
</template>

<script>
// computed is a variable that changes when something changes
import {onMounted} from 'vue'
import {computed} from 'vue';
import {useStore} from 'vuex';
import axios  from 'axios';
import {useRouter} from "vue-router"


export default {
    name: "Nav",
    setup() {
        const router = useRouter();
        const store = useStore();
        const auth = computed(() => store.state.auth)
        const userId = computed(() => store.state.userId)


        const logout = async () => {
            await axios.post('logout', {});
            await store.dispatch('setAuth', false);
            await router.push('/login');
        }

         onMounted(  async () => {
             await axios.get('authenticatedUser').then(response => {
                  store.dispatch('setAuth', true);
                   store.dispatch('setUserId', response.data.authUserId);
                }).catch( (error) => {                   
                            if(error.response){     
                             store.dispatch('setAuth', false);
                            }else{
                                console.log(error)
                            }
                        })
         });

        return {
            auth,
            logout,
            userId
        }
    }
}
</script>



