<template>
<body class="text-center">
  <div class="container">
  <form class="form-signin" @submit.prevent="submit">
      <img class="mb-4" src="https://brokersnest.com/wp-content/uploads/2019/11/ticmill-forex-review-png-300x280.png" alt="" width="72" height="72">
      <h1 class="h3 mb-3 font-weight-normal">{{message}}</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input v-model="email" id="inputEmail" class="form-control" placeholder="Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input v-model="password" id="inputPassword" class="form-control" placeholder="Password" required>
      <div class="checkbox mb-3">
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">Submit</button>
    </form>
  </div>
  </body>
</template>

<script>
import {ref} from 'vue'
import axios from 'axios'
import store from '@/store'
import { useRouter } from 'vue-router';
export default {
name: "Login",
setup(){
  const message = ref('Sign in');
  const email = ref('');
  const password = ref('');
  const router = useRouter()
  const submit = async () => {

  const{data} =   await axios.post('login', {
      email: email.value,
      password: password.value
    }).then(response => {
                  store.dispatch('setAuth', true)
                  router.push('/');
            }).catch( (error) => {     
                            if(error.response){
                              message.value = `${error.response.data.message}`              
                            }else{
                                console.log(error)
                            }
                        })

  }

  return {
    email,
    password,
    submit,
    message
   }
  }
 }
</script>

