<template>
     <!-- THIS SECTION IS FOR ERRORS THAT WOULD COME FROM API -->
            <div v-if="errors">
              <div v-for="error in errors" v-bind:key="error" class="alert alert-danger">{{ error }}</div>
            </div>

<div id="profile">
  <div v-if="auth">
    <div   class="container">
      <h5>{{ message }}</h5>
      <div class="row justify-content">
        <div class="col-md-6">
          <div class="card">
            <div class="card-header">User Profile Card</div>        
              <img class="avatar" :src="avatarImageUrl" alt="avatar">
              <div class="card-body">
              <p class="cardText"> First Name : {{ userName }}</p>
              <p class="cardText"> Last Name : {{ lastName }}</p>
              <p class="cardText"> Email : {{ email }}</p>
              <p class="cardText"> Role : {{ role }}</p>
                </div>
              </div>
            </div>
              <div class="col-md-6">
          <div class="card">
            <div class="card-header">Edit Profile Card</div>
                <div class="card-body ">
                  <form v-if="showForm">
                
                      
            <div class="form-group row">
                  <label for="name" class="col-md-3 col-form-label text-md-right">Name:</label>

                  <div class="col-md-8">
                    <!-- NOTICE v-model="formData.name" - THAT'S HOW IT GETS ATTACHED TO THE FIELD -->
                    <input v-model="formData.name" id="name" type="text" class="form-control" name="name" required autocomplete="name" autofocus>
                  </div>
                </div>

                <div class="form-group row">
                  <label for="lastName" class="col-md-3 col-form-label text-md-right">LastName:</label>

                  <div class="col-md-8">
                    <input v-model="formData.lastName" id="lastName" type="text" class="form-control" name="lastName" required autocomplete="lastName">
                  </div>
                </div>


                <div class="form-group row">
                  <label for="email" class="col-md-3 col-form-label text-md-right">Email:</label>

                  <div class="col-md-8">
                    <input v-model="formData.email" id="email" type="email" class="form-control" name="email" required autocomplete="email">
                  </div>
                </div>

                <div class="form-group row">
                  <label class="col-md-3 col-form-label text-md-right">Avatar:</label>
                  <div class="col-md-9">
                    <div class="custom-file">
                      <input type="file" class="custom-file-input" id="customFile" 
                          ref="photo" @change="handleFileObject()">
                      <label class="custom-file-label text-left" for="customFile">{{ avatarName }}</label>
                  </div>
                </div>
              </div>

                    <div class="form-group row mb-0">
                      <div class="col-md-12">
                        <button @click.prevent="submit" type="submit" class="btn btn-primary" style="background: #42b983; border: #42b983;">
                          Update
                        </button>
                        
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
  <div v-else>
    <div id="card" class="container">
    <div class="card bg-danger text-white text-center">
    <div class="card-header">
      Error Message
    </div>
    <div class="card-body">
      <h5 class="card-title">You are not logged in</h5>
    </div>
    </div>
  </div>
  </div>
</div>


</template>

<script>
  import axios from 'axios'
  import _ from 'lodash'
  import store from '@/store'
  import {computed} from 'vue';

  export default {
 

    data() {
      return {
       formData: {

        },
       auth: computed(() => store.state.auth),
       authenticatedUserId: computed(() => store.state.userId),
       show: false,      
       avatarImageUrl: "",
       avatar: null,
       avatarName: null,
       showForm: true,
       user: null,
       message: " ",
       name: null,
       lastName: null,
       email: null,
       errors: null,
            }
    },
      mounted(){
            axios.get('/getUserProfile').then(res => {
                  this.avatarImageUrl = res.data.avatar_url
                  this.userName = res.data.name
                  this.lastName = res.data.lastName
                  this.email = res.data.email
                  this.role = res.data.role
            })
        },

    methods: {
      submit() {
        this.errors = null
        if(this.$refs.photo){
            let formData = new FormData()
            // <!-- WE APPEND THE AVATAR TO THE FORMDATA WE'RE GONNA POST -->
            formData.append('avatar', this.avatar)

            _.each(this.formData, (value, key) => {
            formData.append(key, value)
            })
            axios.post('/updateUserProfile', formData, {
                headers: {
                'Content-Type': "multipart/form-data; charset=utf-8; boundary=" + Math.random().toString().substr(2)
                }
            }
            ).then(response => {
            //   <!-- OVERRIDING  THE VARIABLES AFTER A SUCCESSFUL API CALL-->
                            this.avatarImageUrl = response.data.avatar_url
                            this.userName = response.data.name
                            this.lastName = response.data.lastName
                            this.email = response.data.email
                            this.message = response.data.message
                            this.showUploadProgress = false
                            this.processingUpload = false
                            this.$emit('imageUrl', response.data.secure_url)
            }) .catch( (error) => {    
            //   <!-- FILLING THE ERRORS ARRAY IN ORDER TO DISPLAY ALL ERROS FROM THE RETURNING API AGTER FROM SUBMISSION-->   
                         this.errors = []
            _.each(error.response.data.errors, error => {
              _.each(error, e => {
                this.errors.push(e)
              })
            })
          })
        }
      },
      handleFileObject() {
        this.message = ""
        this.avatar = this.$refs.photo.files[0]
        this.avatarName = this.avatar.name
      }
    }

  }

</script>