import { Commit, createStore } from 'vuex'

export default createStore({
  
  state: {
    auth: false,
    userId: "",
  },
  mutations: {

    setAuth: (state : {auth: boolean}, auth: boolean) => state.auth = auth,
    setUserId: (state : {userId: string}, userId: string) => state.userId = userId


  },
  actions: {

    setAuth: ( {commit}: {commit:Commit}, auth: boolean) => commit('setAuth', auth),
    setUserId: ( {commit}: {commit:Commit}, userId: string) => commit('setUserId', userId)

  },
  modules: {
  }
})
