<template>

 <!-- CUSTOM FIELD CREATED TO DISPLAY A WELCOME MESSAGE TO THE USER AND ALSO TO BE OVERIDDEN DEPENDING API RESPONSE-->
<div v-if="auth">
 <div class='card' v-bind:class="[className]">
    <div class="card-body">{{ message }}</div>
  </div>

  <div id="transactionsView" class="container">
   <div class="form-group row">
      <div class="col-md-6">
                    <h5 class="text-center font-weight-bold">Add Amount</h5>
        <div class="card bg-dark text-white">
          <div class="card-header">Amount accepts transactions of 1 to 5000</div>

          <div class="card-body">
            <form v-if="showForm">
                <div class="form-group row">
                  <label for="name" class="col-md-4 col-form-label text-md-right">Amount</label>
                  <div class="col-md-6">
                    <input v-model="formData.amount" id="amount" type="text" class="form-control" name="amount" required autocomplete="amount" autofocus>
                  </div>
                </div>

              <div class="form-group row mb-0">
                  <div class="col-md-6 offset-md-4">
                    <button @click.prevent="submit" type="submit" class="btn btn-primary" style="background: #42b983; border: #42b983;">
                      Add Transaction
                    </button>
                    
                  </div>
              </div>
            </form>

  
          </div>
        </div>
      </div>
       <div class="col-md-6 ">
            <h5 class="text-center font-weight-bold">Latest Transactions</h5>
            <table class="table table-borered">
                <thead>
                <tr>
                    <th scope="col">Amount</th>
                    <th scope="col">Date</th>
                </tr>
                </thead>
                <tbody>
                <tr  v-for="transaction in transactions" v-bind:key="transaction.id">
                    <td>{{transaction.amount}}</td>
                    <td>{{transaction.created_at}}</td>
                </tr>
                </tbody>
            </table>
        </div>
   </div>
  </div>
</div>
<div v-else>
  <div id="card" class="container">
  <div class="card bg-danger text-white text-center">
  <div class="card-header">
    Error Message
  </div>
  <div class="card-body">
    <h5 class="card-title">You are not logged in</h5>
  </div>
  </div>
 </div>
</div>

</template>

<script>
import axios from 'axios'
import {ref} from 'vue'
import _ from 'lodash'
import store from '@/store'
import {computed} from 'vue';

    export default {
        data(){
            const message = ref('');
       
            return{
                  isActive: true,
                  className: 'card bg-light',
                  auth: computed(() => store.state.auth),

               formData: {
                amount: null,
           
              },
                showForm: true,
                transactions: [],
                message,
       
            }
        },
        methods: {
              submit() {
                let formData = new FormData()

                   _.each(this.formData, (value, key) => {
                    formData.append(key, value)
                    })  
            axios.post('/addTransaction', formData,
            ).then(response => {
                    this.transactions = response.data.transactions['data'];
                    this.className = 'card bg-success text-white';
                    this.message = response.data.message;
                         
               
            }) .catch( (error) => {                   
                  if(error.response){
                    this.message = error.response.data.errors['amount'][0]
                    this.className = 'card bg-danger text-white';
                    console.log(error.message)
                  }else{
                    console.log(error)
                        }
                         })
      },
            showTransactions(){
                axios.get('getUserTransactions').then(response => {
                 this.transactions = response.data.transactions['data'];
                 this.message = `Logged in as : ${response.data.user.first_name}   ${response.data.user.last_name}`
                });
            }
        },
       mounted(){

                  this.showTransactions();
       }
    }
</script>

